"use client";

import React, { useState } from "react";
import { DollarSign, Copy, Loader2, Check } from "lucide-react";

export default function AISalesAgentPage() {
  const [loading, setLoading] = useState(false);
  const [copied, setCopied] = useState(false);

  const [formData, setFormData] = useState({
    offer: "",
    audience: "",
    tone: "Professional",
    goal: "Qualify Lead",
  });

  const [output, setOutput] = useState("");

  // Handle form inputs
  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>
  ) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  // Handle submission
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setOutput("");

    try {
      const response = await fetch("/api/bots/ai-sales-agent", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      });

      const data = await response.json();
      if (data.message) {
        setOutput(data.message);
      }
    } catch (error) {
      console.error("Error generating:", error);
    } finally {
      setLoading(false);
    }
  };

  // Copy output
  const handleCopy = () => {
    navigator.clipboard.writeText(output);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  return (
    <div className="min-h-screen bg-[#0B1120] text-white p-6">
      <div className="max-w-[1600px] mx-auto grid grid-cols-1 lg:grid-cols-2 gap-8">

        {/* LEFT PANEL — INPUT FORM */}
        <div className="bg-[#1E293B] rounded-2xl border border-gray-800 p-6 shadow-xl h-fit">
          <div className="flex items-center gap-3 mb-6">
            <DollarSign className="text-[#00C26D] h-6 w-6" />
            <h2 className="text-xl font-bold">Sales Agent Config</h2>
          </div>

          <form onSubmit={handleSubmit} className="space-y-6">

            {/* Offer */}
            <div>
              <label className="text-sm text-gray-300 mb-2 block font-semibold">Product / Offer Description *</label>
              <textarea
                name="offer"
                required
                rows={4}
                className="w-full bg-[#0F172A] border border-gray-700 rounded-lg px-4 py-3 text-white focus:ring-1 focus:ring-[#00C26D] outline-none resize-none"
                placeholder="Describe your product or offer..."
                value={formData.offer}
                onChange={handleChange}
              />
            </div>

            {/* Audience */}
            <div>
              <label className="text-sm text-gray-300 mb-2 block font-semibold">Target Audience *</label>
              <input
                type="text"
                name="audience"
                required
                className="w-full bg-[#0F172A] border border-gray-700 rounded-lg px-4 py-3 text-white focus:ring-1 focus:ring-[#00C26D] outline-none"
                placeholder="e.g. CTOs at Tech Companies"
                value={formData.audience}
                onChange={handleChange}
              />
            </div>

            {/* Tone + Goal */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <label className="text-sm text-gray-300 mb-2 block font-semibold">Tone *</label>
                <select
                  name="tone"
                  className="w-full bg-[#0F172A] border border-gray-700 px-4 py-3 rounded-lg text-white focus:ring-1 focus:ring-[#00C26D]"
                  value={formData.tone}
                  onChange={handleChange}
                >
                  <option value="Friendly">Friendly</option>
                  <option value="Professional">Professional</option>
                  <option value="Direct">Direct</option>
                  <option value="Aggressive">Aggressive</option>
                  <option value="Casual">Casual</option>
                </select>
              </div>

              <div>
                <label className="text-sm text-gray-300 mb-2 block font-semibold">Goal *</label>
                <select
                  name="goal"
                  className="w-full bg-[#0F172A] border border-gray-700 px-4 py-3 rounded-lg text-white focus:ring-1 focus:ring-[#00C26D]"
                  value={formData.goal}
                  onChange={handleChange}
                >
                  <option value="Book a Call">Book a Call</option>
                  <option value="Qualify Lead">Qualify Lead</option>
                  <option value="Warm Outreach">Warm Outreach</option>
                </select>
              </div>
            </div>

            {/* Button */}
            <button
              type="submit"
              disabled={loading}
              className="w-full bg-[#00C26D] hover:bg-[#00A05A] text-white font-bold py-3 rounded-lg transition-all flex items-center justify-center gap-2"
            >
              {loading ? <Loader2 className="animate-spin h-5 w-5" /> : "Generate Sales Message"}
            </button>
          </form>
        </div>

        {/* RIGHT PANEL — OUTPUT */}
        <div className="bg-[#1E293B] rounded-2xl border border-gray-800 p-6 shadow-xl min-h-[400px] flex flex-col">
          <div className="flex justify-between items-center mb-6 border-b border-gray-800 pb-4">
            <h2 className="text-xl font-bold">Agent Output</h2>

            <button onClick={handleCopy} disabled={!output} className="text-sm text-[#00C26D] flex items-center gap-2">
              {copied ? <Check className="h-4 w-4" /> : <Copy className="h-4 w-4" />}
              {copied ? "Copied" : "Copy"}
            </button>
          </div>

          <div className="flex-1 bg-[#0F172A] rounded-xl p-6 border border-gray-800 text-gray-300 whitespace-pre-wrap">
            {loading ? (
              <div className="flex items-center justify-center h-full text-gray-500">Generating message...</div>
            ) : output ? (
              output
            ) : (
              <div className="flex items-center justify-center h-full text-gray-600">Response will appear here.</div>
            )}
          </div>
        </div>

      </div>
    </div>
  );
}
